* {
  box-sizing: border-box;
  font-family: Josefin Sans;
}
#header {
  width: 100%;
  height: 60vh;
  margin-top: 70px;
}
#header h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header .brand-name {
  color: #3498db;
}
.header h2 {
  color: #484848;
  font-size: 24px;
}
.btn-get-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding-top: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
  padding: 11px 33px;
}
.btn-get-started:hover {
  background-color: #3498db;
  color: white;
}
.header-img {
  text-align: right;
}
#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
.menu_active {
  border-bottom: 1px solid black;
}
